import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Main from '../views/Main.vue'
import MainB from '../views/MainB.vue'
import Rank from '../views/Rank.vue'
import Prize from '../views/Prize.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/main',
    name: 'Main',
    component: Main
  },
  {
    path: '/mainb',
    name: 'MainB',
    component: MainB
  },
  {
    path: '/rank',
    name: 'Rank',
    component: Rank
  },
  {
    path: '/prize',
    name: 'Prize',
    component: Prize
  }
]

const router = new VueRouter({
  mode: 'hash',
  routes
})

export default router
