          <div class="zhou">4人</div>
          <div class="zhou">8人</div>
          <div class="zhou">12人</div>
          <div class="zhou">16人</div>
          <div class="zhou">20人</div>
        </div>
        <div class="bar_wrap">
          <div class="bar" :style="{ width: 20 * state + '%' }"></div>
          <div class="bar_q">
            <span :class="{ active: state > 0 }"></span>
            <span :class="{ active: state > 1 }"></span>
            <span :class="{ active: state > 2 }"></span>
            <span :class="{ active: state > 3 }"></span>
            <span :class="{ active: state > 4 }"></span>
          </div>
        </div>
        <div class="user_box">
          <div class="user" v-for="(item, index) in state" :key="index">
            <img v-if="index == 4" src="../assets/images/newbtn_10.png" />
            <img v-else src="../assets/images/newbtn_13.png" />
          </div>
          <div
            class="user_btn"
            @click="adduser"
            v-for="(item, index) in users - state"
            :key="index + state"
          >
            <p class="user_btn_text">{{ index + state + 1 }}</p>

            <img v-if="index == 0" src="../assets/images/newbtn_09.png" />
            <img v-else src="../assets/images/newbtn_03.png" />
          </div>
        </div>
        <img
          class="share_btn"
          @click="createPoster"
          v-if="state !== 5"
          src="../assets/images/yao.png"
        />
        <img
          class="share_text"
          v-if="state == 5"
          @click="createPoster"
          src="../assets/images/yao2.png"
        />
      </div>
    </div>
    <div class="banner">
      <img src="../assets/images/hwei_04.jpg" />
      <img class="btn_me" @click="gome" src="../assets/images/me_btn.png" />

      <img
        v-if="state > 0"
        class="btn_play"
        @click="goprize"
        src="../assets/images/played.png"
      />
      <img v-else class="btn_play" src="../assets/images/play.png" />
      <img class="btn_rank" @click="gorank" src="../assets/images/rank.png" />
      <!-- <div class="tip_box" v-if="state == 1">
        <img src="../assets/images/tip.png" />
        <p>你已合力<span>4</span>次，获得<span>1</span>次抽奖资格</p>
      </div> -->
    </div>
    <div class="banner">
      <img src="../assets/images/hwei_05.jpg" />
      <div class="ruler_btn" @click="showRuler"></div>
      <a class="ruler_btn2" href="https://f-mobile.infinitus.com.cn/home"> </a>
    </div>

    <!-- 规则弹框 -->
    <ruler :ruleShow="isShowModal" @close="isShowModal = false" />
    <!-- 分享海报 -->
    <div class="posterHtml" id="posterHtml">
      <img class="cover" src="../assets/images/cover.png" />
      <div class="qrcode"><div id="qrcodeImg"></div></div>
    </div>
    <div class="post_box" v-if="cover_url">
      <img class="post_img" :src="cover_url" />
      <p class="post_tip">长按保存图片转发给好友</p>
      <img
        class="colse_btn"
        @click="cover_url = ''"
        src="../assets/images/close_btn.png"
      />
    </div>
  </div>
</template>
<script>
import marquee from "../components/marquee";
import ruler from "../components/ruler";
import QRCode from "qrcodejs2";
import html2canvas from "html2canvas";
export default {
  data() {
    return {
      cover_url: "",
      count: 1,
      user_id: "",
      code_url: `https://varshiya.github.io/#/mainb/?id=123`,
      posterHtmlBg: require("../assets/images/cover.png"),
      isShowModal: false,
      state: 1,
      users: 5,
    };
  },
  components: {
    marquee,
    ruler,
  },

  created: function () {
    const id = this.$route.query.id;
    if (id) {
      this.user_id = id;
    }
  },
  mounted: function () {
    this.createQrcode();
    const interval = setInterval(() => {
      this.count++;
      if (this.count === 8) {
        clearInterval(interval);
      }
    }, 800);
  },
  methods: {
    adduser() {
      this.state = this.state + 1;
    },

    createQrcode() {
      const qrcodeImgEl = document.getElementById("qrcodeImg");
      qrcodeImgEl.innerHTML = "";
      new QRCode(qrcodeImgEl, {
        text: this.code_url,
        width: 80,
        height: 80,
        colorDark: "#000000",
        colorLight: "#ffffff",
        correctLevel: QRCode.CorrectLevel.L,
      });
    },
    createPoster() {
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      const vm = this;
      const domObj = document.getElementById("posterHtml");
      html2canvas(domObj, {
        useCORS: true,
        allowTaint: false,
        backgroundColor: "transparent",
      }).then(function (canvas) {
        vm.cover_url = canvas.toDataURL("image/png");
      });
    },

    showRuler() {
      this.isShowModal = true;
      console.log(this.isShowModal);
    },
    gorank() {
      this.$router.push({ path: "rank", query: { id: 1 } });
    },
    gome() {
      this.$router.push({ path: "rank", query: { id: 2 } });
    },
    goprize() {
      this.$router.push({ path: "prize" });
    },
  },
};
</script>
